// 内容面板状态枚举
export enum ContentPanelState {
    // 关闭
    Closed = 0,

    // 最小化
    Minimize = 1,

    // 正常
    Normal = 2,

    // 最大化
    Maximize = 3
}

// 导航菜单状态枚举
export enum NavMenuState {
    // 隐藏
    Hidden = 0,

    // 展开
    Expand = 1,

    // 折叠
    Collapsed = 2
}